import React, { Fragment } from "react";
import { AccessControlTypes, Translation } from "../../types";
import { Button } from "antd";
import {
    ModalContent,
    ModalOpenButton,
    useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";

export default function NewTranslationModal() {
    const { onOk, form } = useModalContext();

    function handleOk() {
        onOk("/translation", "create", {} as Translation);
    }

    return (
        <Fragment>
            <ModalOpenButton requiredRoles={["Administrator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
                <Button children="Create New Language Translation" />
            </ModalOpenButton>
            <ModalContent title="Create new language translation" onClickOk={handleOk} formName="create_translation_form">
                <Form schemaName="translation">
                    <FormContentBase
                        name="create_translation_form"
                        form={form}
                        preserve={false}
                    />
                </Form>
            </ModalContent>
        </Fragment>
    );
}
